@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root{--light:#6f74dd;--main:#3949ab;--dark:#00227b;--dark-primary:#283655;--dark-light:#4d648d    ;--dark-dark: #1e1f26;--bg-color:#408697}.game{max-width:500px;margin:0 auto}.body{display:flex;justify-content:center;-ms-scroll-chaining:none;overscroll-behavior:contain;overflow:hidden;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}body{background-color:#4b86b4}.help{color:#ffffff;margin:10px;text-align:center;font-family:'Pacifico';font-size:200%}.left{text-align:center;width:50%;border:thin solid #ddd}.right{text-align:center;padding:10px;width:50%;border:thin solid #ddd}.star{display:inline-block;margin:0 15px;color:#ffffff}.star:after{content:"\2605";font-size:45px}.number{background-color:#eee;border:thin solid #ddd;width:45px;height:45px;margin:10px;font-size:25px}.number:focus,.number:active{outline:none;border:thin solid #ddd}.timer{margin-top:3px;margin-left:3px;color:#ffffff;text-align:center;font-family:'Pacifico';font-size:200%}.game-done .message{font-family:'Pacifico';font-size:250%;margin:15px}button{display:inline-block;margin:0 auto;border:none;margin:0;text-decoration:none;background:#283655;background:var(--dark-primary);color:#ffffff;font-family:'Pacifico',sans-serif;font-size:1rem;line-height:1;cursor:pointer;text-align:center;transition:background 250ms ease-in-out, transform 150ms ease;-webkit-appearance:none;-moz-appearance:none}

